body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  width: 340px;
  box-shadow: 2px 2px 8px rgb(73, 69, 69);
  margin-left: auto;
  margin-right: auto;
  margin-top: 30px;
  font-size: 18px;
  font-weight: bold;
}
nav {
  text-align: center;
  padding-top: 15px;
}

li {
  display: inline-block;
  margin: 2px 14px;
  padding-bottom: 14px;
  font-weight: 500;
}

.body > li {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: center;
  display: block;
  padding-left: 4px;
  padding-right: 2px;
  width: 300px;
  background-color: #ddd;
  border-radius: 10px;
  margin: 16px 6px;
  font-weight: 500;
}
.body {
  padding: 12px;
  margin-top: 0;
}

button{
  padding: 9px;
  border-radius: 12px;
margin-left: 110px;
background-color: crimson;
color: white;
border-style: none;
margin-bottom: 12px;
}